@import '../../styles/global/mixins';
@import '../../styles/global/variables';

.projects {
    // justify-content: center;
    // grid-column: container-start / col-end 8;
    grid-column: container-start / col-end 12;
    padding: 10rem 0;

    // border: dotted;

    @include breakpoint(md){
        grid-column: container-start / container-end;
    }

    &__grid{
        display: grid;
        grid-row-gap: 10rem;
    }

    hr{
        border: none;
        border-bottom: 1px solid rgb(201, 201, 201);
    }
    
}

.project{
    display: grid;
    grid-template-columns: 30rem 1fr; // sizes col-1
    grid-gap: 8rem;

    // align-items: center;
    // grid-column: flush-start / flush-end;
    // justify-content: center;

    @include breakpoint(md){
        grid-template-columns: 1fr;
    }

    .col-1{
        // border: solid;
        // background-color: pink;
    }

    .col-2{
        display: grid;
        grid-row-gap: 1rem;
    }

    &__display{
        margin-bottom: 2rem;

        &__poster{
            height: 20rem;
            overflow: hidden;
            margin-bottom: 1.5rem;
            box-shadow: 0 .8rem 1rem rgba(black, .3);

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
            }
        }

        &__actions{
            display: flex;

            a{
                &:not(:last-child){
                    margin-right: 2.5rem;
                }
            }
        }
    }


    &__technologies{
        
        &__list{
            display: grid;
            list-style: none;
            grid-template-columns: repeat(2, 1fr);
            grid-row-gap: 1.5rem;

            &--no-grid{
                grid-template-columns: 1fr;
            }
            
            li{
                display: flex;
                i{
                    font-size: 1rem;
                    margin-right: .5rem;
                    color: $color-primary;
                    line-height: 2rem;
                }
            }
        }
    }
}

.aside{
    font-size: 1.4rem;
    margin-top: 2rem;
    line-height: 1.9rem;
    color: rgb(31, 32, 36);
    font-weight: 500;
}